/*
	The Events Calendar Styles for LSX
*/

@import '../scss/global/variables';
@import '../scss/global/mixins';

#tribe-events {
	.tribe-events-back {
		a {
			// @TODO - Replace by button mixin
			//@include lsx-button;
		}
	}

	.tribe-events-button {
		// @TODO - Replace by colour variables
		//background: $primary !important;

		&:hover,
		&:active,
		&:focus {
			// @TODO - Replace by colour variables
			//background: $primary-darker !important;
		}
	}

	.tribe-events-event-meta {
		.tribe-event-schedule-details {
			margin-bottom: 10px;
		}

		.tribe-events-venue-details {
			line-height: 1.6;
		}
	}

	.tribe-events-schedule {
		h2 {
			margin: 0;
    		font-size: 20px;
		}

		.tribe-events-divider {
			display: none;
		}
	}

	.tribe-events-page-title {
		margin-top: 0;
	}

	.tribe-events-event-image {
		@media (min-width: 768px) {
			float: left;
			margin: 0 30px 40px 0;
		}

		img {
			max-width: 500px;
		}
	}

	.tribe-events-list {
		.tribe-events-event-cost {
			span {
				// @TODO - Replace by colour variables
				//background: $primary-darker;
				//color: $white;
				border: none;
				border-radius: 3px;
			}
		}
	}

	.tribe-events-single-section {
		clear: both;
	}
}
